          </FormItem>
          <FormItem>
            <Select v-model="formData.subjectId" style="width: 120px" @on-change="changeSubject" clearable placeholder="">
              <Option v-for="(item, index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.planAdvisorNo" filterable clearable remote :remote-method="getTreatAdvisorEmpNos" label-in-value style="width: 150px" :loading="loading1" placeholder="">
              <Option v-for="item in treatAdvisorEmpNos" :value="item.userWorkCode" :key="item.userWorkCode">{{ item.userName }} - {{ item.userWorkCode }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.treatAdvisorNo" filterable clearable remote :remote-method="getTreatAdvisorEmpNos" label-in-value style="width: 150px" :loading="loading1" placeholder="">
              <Option v-for="item in treatAdvisorEmpNos" :value="item.userWorkCode" :key="item.userWorkCode">{{ item.userName }} - {{ item.userWorkCode }}</Option>
