            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.channelId" style="width: 120px" clearable placeholder="">
              <Option v-for="(item, index) in diagnosisChannels" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classType" style="width: 120px" clearable placeholder="">
              <Option :value="1"></Option>
              <Option :value="4"></Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isRegist" style="width: 120px" @on-change="changeSubject" clearable placeholder="">
              <Option v-for="(item, index) in conditions.isRegist" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isPay" style="width: 120px" @on-change="changeSubject" clearable placeholder="">
              <Option v-for="(item, index) in conditions.isPay" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.appointmentStatus" style="width: 120px" @on-change="changeSubject" clearable placeholder="">
              <Option v-for="(item, index) in conditions.appointmentStatus" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isAttendance" style="width: 120px" @on-change="changeSubject" clearable placeholder="">
              <Option v-for="(item, index) in conditions.attendanceStatus" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>

          <FormItem>
            <DatePicker type="daterange" :editable="false" v-model="formData.createTime" style="width: 200px" @on-change="
                optDateChange($event, 'createStartTime', 'createEndTime')
              " placeholder=""></DatePicker>
          </FormItem>
          <FormItem>
            <DatePicker type="daterange" :editable="false" v-model="formData.auditionTime" style="width: 200px" placeholder="" @on-change="
                optDateChange($event, 'auditionStartTime', 'auditionEndTime')
              "></DatePicker>
          </FormItem>
        </template>
        <FormItem>
          <Input v-model="formData.stuRealName" clearable placeholder="" />
        </FormItem>
