        </FormItem>
        <template v-if="activeTabName !== 'reservationRecord'">
          <FormItem>
            <DatePicker type="daterange" :editable="false" v-model="formData.applyTime" style="width: 200px"
              @on-change="optDateChange($event, 'createTime', 'endTime')" placeholder="">
            </DatePicker>
         </FormItem>
        </template>
        <div style="text-align: right; margin-bottom: 10px; margin-top: -30px">
          <Button v-if="roleId === 5" type="primary" style="margin-right: 10px" @click="createRrCode"></Button>
          <Button type="primary" style="margin-right: 10px" @click="onSearch"></Button>
          <Button v-if="activeTabName === 'reservationRecord'" type="primary" style="margin-right: 10px" :loading="btnLoading" @click="exportDataClick"></Button>
          <Button v-if="activeTabName === 'applyRecord'" type="primary" style="margin-right: 10px" :loading="btnLoading" @click="exportApplyDataClick"></Button>
        </div>
      </div>
    </Form>
    <div style="background: #fff;margin-top: 20px;padding: 10px;">
      <Tabs type="card" :animated="false" name="audition" v-model="activeTabName">
        <TabPane :label="customTabPaneLabel" name="applyRecord">
          <div class="flex">
            <span style="margin-right: 10px;">: </span>
            <CheckboxGroup v-model="formData.applyStatusList" @on-change="onSearch">
                <Checkbox :label="0"><span></span></Checkbox>
                <Checkbox :label="3"><span></span></Checkbox>
                <Checkbox :label="2"><span></span></Checkbox>
            </CheckboxGroup>
          </div>
          <div class="flex">
            <span style="margin-right: 10px;">: </span>
            <Select style="width: 150px;" v-model="formData.orderSortType" @on-change="onSearch">
              <Option v-for="(item, index) in sortTypeList" :key="index" :value="item.value">{{item.label}}</Option>
            </Select>
          </div>
          <ApplyRecord :formData="formData" ref="applyRecord"></ApplyRecord>
        </TabPane>
        <TabPane label="" name="reservationRecord">
          <ReservationRecord :formData="formData" ref="reservationRecord"></ReservationRecord>
        </TabPane>
      </Tabs>
