// 
import { getCityList, queryTermListForBaseData, getYearList, getGradeList, getSubjectList, getServiceCenterList, getTimeTypeList, getTimeList, courseLinkageLink } from '@/api/bd'
import { getEmployeeList } from '@/api/specialRefund'

export default {
  data () {
    return {
      cityList: [], // 
      yearList: [], // 
      termList: [], // 
      gradeList: [], // 
      subjectList: [], // 
      serviceCenterList: [], // 
      advisorEmpNos: [], // 
      classLevelList: [], // 
      timeTypeList: [], // 
      timeList: [] // 
    }
  },
  methods: {
    //  9:00~11:00 ...
    getTimeList () {
      getTimeList().then(res => {
        this.timeList = Object.freeze(res)
      }).catch(error => {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      })
    },
    //    ...
    getTimeTypeList () {
      getTimeTypeList().then(res => {
        this.timeTypeList = Object.freeze(res)
      }).catch(error => {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      })
    },
    // 
    queryLevelDegreeList () {
      const params = {
        schoolCode: this.formData.cityCode,
        status: 0
      }
      courseLinkageLink(params).then(res => {
        this.classLevelList = Object.freeze(res)
      }).catch(error => {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      })
    },
    // 
    getCityList () {
      return getCityList()
        .then(data => {
          this.cityList = data
          if (this.cityList.length) {
            this.formData.cityCode = data[0].id
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },
    // 
    getAdvisorEmpNos (query, cityCode) {
      if (!cityCode) {
        this.$Message.info({ content: '' })
        return
      }
      const params = {
        cityCode: cityCode,
        name: isNaN(Number(query)) ? query : '',
        empNo: isNaN(Number(query)) ? '' : query
      }
      getEmployeeList(params).then(res => {
        this.advisorEmpNos = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    // 
    async getServiceCenterList (likeName, cityCode) {
      try {
        if (!cityCode) {
          this.$Message.info({ content: '' })
          return
        }

        const params = {
          cityCode,
          likeName
        }
        const data = await getServiceCenterList(params)
        this.serviceCenterList = Object.freeze(data) || []
      } catch (error) {
        this.$Message.warning({
          content: ':' + error.message,
          duration: 5
        })
      }
    },
    // 
    async getSubjectList () {
      try {
        const params = {
          cityCode: this.formData.cityCode
        }
        const data = await getSubjectList(params)
        this.subjectList = Object.freeze(data)
      } catch (error) {
        this.$Message.warning({
          content: ':' + error.message,
          duration: 5
        })
      }
    },
    // 
    async getGradeList () {
      try {
        const params = {
          cityCode: this.formData.cityCode
        }
        const data = await getGradeList(params)
        this.gradeList = Object.freeze(data)
      } catch (error) {
        this.$Message.warning({
          content: ':' + error.message,
          duration: 5
        })
      }
    },
    // 
    async getYears () {
      try {
        const data = await getYearList()
        this.yearList = Object.freeze(data)
      } catch (error) {
        this.$Message.warning({
          content: ':' + error.message,
          duration: 5
        })
      }
    },
    // 
    async getBaseDataTermList () {
      try {
        const params = {
          cityCode: this.formData.cityCode
        }
        const data = await queryTermListForBaseData(params)
        this.termList = Object.freeze(data)
      } catch (error) {
        this.$Message.error({ content: ':' + error.message, duration: 5 })
      }
    }
  }
}
