import {
  getAreaList,
  getCityList,
  getYearList,
  getLinkage,
  getServiceCenterList,
  getVenueList,
  getClassType,
  getTeacherByNameFuzzy
} from '@/api/bd'
import Cookie from 'js-cookie'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'

// import { getRefundFistLevel, getRefundSecondLevel } from '@/api/refund'
// import {
//   getRefundType,
//   operationPlatform,
//   changeClassCourseOperationPlatform
// } from '@/api/reason'
// import { getChangeClassType, queryChangeCourseType } from '@/api/register'

export default {
  data () {
    return {
      allCityList: [], // 
      yearList: [], // 
      termList: [], // 
      subjectList: [], // 
      gradeTypeList: [], // 
      gradeList: [], // 
      levelList: [], // 
      areaList: [], // 
      serviceList: [], // 
      teacherDotList: [], // 
      classTypeList: [], // 
      teacherNameList: [], // 
      tutorNameList: [], // 
      loading1: false, // 
      // 
      refundReason: [], // 
      secodeRefundReason: [], // 
      refundType: [], // 
      operationPlatform: [], // 
      changeClassType: [], // 
      changeCourseType: [], // 
      classFlag: [
        // 
        {
          value: 0,
          label: ''
        },
        {
          value: 1,
          label: ''
        }
      ],
      // 
      formData: {
        type: 0,
        typeName: '',
        cityCode: '', // 
        year: new Date().getFullYear() + '', // 
        termId: '', // 
        gradeTypeId: '', // 
        gradeId: '', // 
        subjectId: '', // 
        levelId: '', // 
        areaId: '', // 
        serviceCenterId: '', // 
        venueId: '', // 
        teacherId: '', // 
        tutorId: '', // 
        startDate: '', // 
        startDateVal: '',
        endDate: '',
        endDateVal: '',
        secondReasonId: '', // 
        firstReasonId: '', // 
        refundType: '', // 
        operatePlatform: '', // 
        operateStartDate: '',
        operateStartDateVal: '',
        operateEndDate: '',
        operateEndDateVal: '',
        classFlag: '', // 
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      formDataSource: {
        type: 0,
        typeName: '',
        cityCode: '', // 
        year: new Date().getFullYear() + '', // 
        termId: '', // 
        gradeTypeId: '', // 
        gradeId: '', // 
        subjectId: '', // 
        levelId: '', // 
        areaId: '', // 
        serviceCenterId: '', // 
        venueId: '', // 
        teacherId: '', // 
        tutorId: '', // 
        startDate: '', // 
        startDateVal: '',
        endDate: '',
        endDateVal: '',
        secondReasonId: '', // 
        firstReasonId: '', // 
        refundType: '', // 
        operatePlatform: '', // 
        operateStartDate: '',
        operateStartDateVal: '',
        operateEndDate: '',
        operateEndDateVal: '',
        classFlag: '', // 
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      setDefaultServiceCenter: false // 
    }
  },
  methods: {
    // 
    optDateChange (val, start, end) {
      this.formData[start] = val[0]
      this.formData[end] = val[1]
    },
    // 
    getChangeCourseTypeHandler () {
      const params = {
        cityCode: this.formData.cityCode
      }
      queryChangeCourseType(params)
        .then(res => {
          let arr = []
          res.forEach(item => {
            arr.push({
              value: item.chanageCourseTypeId,
              label: item.changeCourseTypeName,
              desc: item.description
            })
          })
          this.changeCourseType = Object.freeze(arr)
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 3
          })
        })
    },
    // 
    getChangeClassTypeHandler () {
      const params = {
        cityCode: this.formData.cityCode
      }
      getChangeClassType(params)
        .then(res => {
          let arr = []
          res.forEach(item => {
            arr.push({
              value: item.chanageClassTypeId,
              label: item.changeClassTypeName,
              desc: item.description
            })
          })
          this.changeClassType = Object.freeze(arr)
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 3
          })
        })
    },
    // 
    operationPlatformHandler () {
      let apiOperationPlatform
      if (!this.formFields) {
        return
      } else {
        if (this.formFields.has('refundType')) {
          apiOperationPlatform = operationPlatform
        } else {
          apiOperationPlatform = changeClassCourseOperationPlatform
        }
      }

      apiOperationPlatform()
        .then(res => {
          this.operationPlatform = Object.freeze(res)
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 3
          })
        })
    },
    // 
    getRefundTypeHandler () {
      getRefundType()
        .then(res => {
          this.refundType = Object.freeze(res)
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 3
          })
        })
    },
    changeFirstReason (firstReasonId) {
      this.secodeRefundReason = []
      this.formData.secondReasonId = ''
      if (!firstReasonId) return false

      const items = this.refundReason.filter(
        item => item.value === firstReasonId
      )

      if (items && items[0]) {
        if (!items[0].children) {
          this.getRefundSecondLevelHandler(items[0])
        } else {
          this.secodeRefundReason = [...items[0].children]
        }
      }
    },
    buildReason (res, hasChild) {
      let _res = []
      res.forEach(obj => {
        let _d = {
          value: obj.id,
          label: obj.reason,
          desc: obj.description
        }
        _res.push(_d)
      })
      return _res
    },
    // 
    getRefundSecondLevelHandler (item) {
      getRefundSecondLevel({
        parentId: item.value,
        cityCode: this.formData.cityCode
      })
        .then(res => {
          if (res && res.length) {
            item.children = this.buildReason(res)
            this.secodeRefundReason = [...item.children]
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 3
          })
        })
    },
    // 
    getRefundFistLevelHandler () {
      getRefundFistLevel({
        cityCode: this.formData.cityCode
      })
        .then(res => {
          if (res && res.length) {
            this.refundReason = this.buildReason(res, true)
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 3
          })
        })
    },
    //   
    // 
    getCitys () {
      getCityList()
        .then(data => {
          this.allCityList = data
          if (this.allCityList.length && this.setDefaultServiceCenter) {
            this.formData.cityCode = data[0].id
            this.getBaseData()
            this.cityYearChange(this.formData.cityCode)
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },
    // 
    getYears () {
      getYearList()
        .then(data => {
          this.yearList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },
    // 
    getClassTypeList () {
      let req = {}
      getClassType(req)
        .then(res => {
          this.classTypeList = res
        })
        .catch(err => {
          this.$Message.warning({
            content: ':' + err.message,
            duration: 5
          })
        })
    },
    // 
    getBaseData () {
      // t gt g s ,cd c 
      // cityCode
      let toNum = {
        cityCode: this.formData.cityCode
      }
      getAreaList(toNum)
        .then(res => {
          this.areaList = res
          // 
          if (this.setDefaultServiceCenter) {
            queryServiceCenterByEmpNo({
              cityCode: this.formData.cityCode,
              empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
            }).then(data => {
              if (this.areaList.filter(a => a.schoolId === data.areaId).length > 0) {
                this.formData.areaId = data.areaId
                this.changeArea(data.areaId, data.serviceCenterId)
              } else {
                this.formData.areaId = ''
                this.serviceCenterList = []
                this.formData.serviceCenterId = ''
              }
            }).catch(err => {
              this.$Message.error(':' + err.message)
            })
          }
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })
    },
    // 
    cityYearChange (data) {
      console.log('city change', data)
      this.formData.termId = ''
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.levelId = ''
      this.formData.classType = null
      this.formData.teacherId = ''
      this.formData.tutorId = ''
      this.formData.subjectId = ''
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.formData.venueId = ''
      this.formData.secondReasonId = ''
      this.formData.firstReasonId = ''
      this.formData.refundType = ''
      this.formData.operatePlatform = ''
      if (!data) {
        return
      }
      this.getBaseData()
      let baseTerm = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      getLinkage(baseTerm)
        .then(res => {
          this.termList = res
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })

      if (this.formFields) {
        if (this.formFields.has('refundType')) {
          this.getRefundFistLevelHandler()
          this.getRefundTypeHandler()
        }

        if (this.formFields.has('changeClassType')) {
          this.getChangeClassTypeHandler()
        }

        if (this.formFields.has('changeCourseType')) {
          this.getChangeCourseTypeHandler()
        }
      }

      // - //
      // this.operationPlatformHandler()
    },
    // 
    changeYear (val) {
      this.gradeTypeList = []
      this.formData.gradeTypeId = ''
      this.termList = []
      this.formData.termId = ''
      this.formData.gradeId = ''
      this.gradeList = []
      this.formData.subjectId = ''
      this.subjectList = []
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      let baseTerm = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      getLinkage(baseTerm)
        .then(res => {
          this.termList = res
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })
    },
    // 
    changeTerm (val) {
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      getLinkage(req)
        .then(res => {
          this.gradeTypeList = res
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })
    },
    // 
    gradeTypeChange (val) {
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      getLinkage(req)
        .then(res => {
          this.gradeList = res
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })
    },
    // 
    changeClasses (val) {
      this.formData.subjectId = ''
      this.subjectList = []
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      getLinkage(baseSubject)
        .then(res => {
          this.subjectList = res
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })
    },
    // 
    changeSubject (val) {
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      let baseClassCount = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gradeIds: this.formData.gradeId,
        subjectIds: this.formData.subjectId,
        queryType: 'c'
      }
      getLinkage(baseClassCount)
        .then(res => {
          this.levelList = res
        })
        .catch(err => {
          this.$Message.error(':' + err.message)
        })
    },
    // 
    changeArea (val, serviceCenterId) {
      this.serviceList = []
      this.formData.serviceCenterId = ''
      if (val === 'undefined' || !this.formData.areaId) {
        return false
      }
      let ser = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      getServiceCenterList(ser)
        .then(res => {
          this.serviceList = res
          this.serviceList.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })

          if (serviceCenterId) {
            this.formData.serviceCenterId = serviceCenterId
          }
        })
        .catch(err => {
          this.$Message.warning(':' + err.message)
        })
    },
    // 
    changeService (val) {
      this.teacherDotList = []
      this.formData.venueId = ''
      if (
        val === 'undefined' ||
        !this.formData.serviceCenterId ||
        !this.formData.areaId
      ) {
        return false
      }
      let serChange = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      getVenueList(serChange)
        .then(res => {
          this.teacherDotList = res
        })
        .catch(err => {
          this.$Message.warning(':' + err.message)
        })
    },
    // 
    getTeacherName (query) {
      if (!this.formData.cityCode) {
        this.$Message.error('')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.classType,
        pageNo: 1,
        pageSize: 20
      }
      getTeacherByNameFuzzy(req)
        .then(res => {
          this.teacherNameList = res.records
          this.loading1 = false
        })
        .catch(err => {
          this.loading1 = false
          this.$Message.error(':' + err.message)
        })
    },
    // 
    getTutorName (query) {
      if (!this.formData.cityCode) {
        this.$Message.error('')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.tutorNameList = []
      let req = {
        sysName: query,
        teacherType: 1,
        cityCode: this.formData.cityCode,
        businessType: this.formData.classType,
        pageNo: 1,
        pageSize: 20
      }
      getTeacherByNameFuzzy(req)
        .then(res => {
          this.tutorNameList = res.records
          this.loading1 = false
        })
        .catch(err => {
          this.loading1 = false
          this.$Message.error(':' + err.message)
        })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },

    getClassTypeName (val) {
      return this.classTypeMap[val]
    },
    // 
    startDateChange (val, startName = 'startDate', endName = 'endDate') {
      if (this.formData[endName]) {
        if (
          new Date(val).getTime() > new Date(this.formData[endName]).getTime()
        ) {
          this.$Message.warning({
            content: '',
            duration: 5
          })
          this.formData[startName] = ''
          this.formData[startName + 'Val'] = ''
          return
        }
      }
      this.formData[startName] = val
    },
    endDateChange (val, startName = 'startDate', endName = 'endDate') {
      if (this.formData[startName]) {
        if (
          new Date(this.formData[startName]).getTime() > new Date(val).getTime()
        ) {
          this.$Message.warning({
            content: '',
            duration: 5
          })
          this.formData[endName] = ''
          this.formData[endName + 'Val'] = ''
          return
        }
      }
      this.formData[endName] = val
    }
  }
}
