        @queryParams="getQueryParams"
      />
    </div>
    <!--  -->
    <div class="cuanban-table">
       <CuanbanTable ref="cuanbanTableRef" :queryParams='queryParams' @updateCuanbanDemand="updateCuanbanDemand"></CuanbanTable>
    </div>
    <!--  -->
    <CreateClass
      ref="childCreateClass"
      :oldFormData="oldCuanbanDemand"
