      //   name: '', // 
      //   phone: '', // 
      //   studentCard: '', // 
      //   time: '',
      //   startCreateDate: '',
      //   endCreateDate: ''
      // }
    }
  },
  created () {

  },
  mounted () {

  },
  methods: {
    //  
    openAddClassHandler () {
      this.$refs.childCreateClass.addClassHandler()
    },
    //  engine
    openStudentListHandler () {
      this.$refs.childStudentList.studentListHandler()
    },
    //  
    openBatchImportHandler () {
      this.$refs.childBatchImportWrapper.batchImportWrapperHandler()
    },
    //  
    createCuanbanClass () {
      this.openAddClassHandler()
      this.createClassType = 1
      this.createClassTypeName = ''
    },
    // 
    updateCuanbanDemand (data) {
      console.log(data, '----')
      this.oldCuanbanDemand = data // id
      this.openAddClassHandler()
      this.createClassType = 2
      this.createClassTypeName = ''
    },
    // 
    updateTableData () {
      this.$refs.classSearchComponent.getAssembleTotal()
      this.$refs.cuanbanTableRef.queryTableData(this.$refs.classSearchComponent.formData)
    },
    getQueryParams (params, status, isClear) {
      this.queryParams = { ...params, '_status': status, '_clear': isClear }
    }
  }
}
</script>

