<template>
  <!--  -->
  <div style="width: 100%;padding: 15px;">
    <!-- type: 1   -->
    <div
      class="stu-search"
      style="margin-bottom: 30px;"
      v-if="type === 1"
    >
      <TalStuSearch
        ref="stuSearch"
        :stu-card="queryData.card"
        :stu-city-code="queryData.cityCode"
        :showCrmInfo="false"
        @stuInfo="setStuInfo"
      ></TalStuSearch>
    </div>

    <p v-if="!formData.uid"></p>
    <template v-if="formData.uid && (type === 1 || type == 2)">
      <Form
        inline
        :label-width="130"
        ref="formValidate"
        :model="formData"
        :rules="ruleValidate"
        style="margin-bottom: 20px;"
      >
        <FormItem
          label=""
          prop="year"
        >
          <Select
            v-model="formData.year"
            style="width: 150px;"
            clearable
          >
            <Option
              v-for="(item,index) in yearList.slice(1, -1)"
              :value="item"
              :key="index"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="termId"
        >
          <Select
            v-model="formData.termId"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'termName')"
          >
            <Option
              v-for="(item,index) in termList.slice(0, 4)"
              :value="item.standardId"
              :key="index"
            >{{ item.standardName }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="gradeId"
        >
          <Select
            v-model="formData.gradeId"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'gradeName')"
          >
            <Option
              v-for="(item,index) in gradeList"
              :value="item.standardId"
              :key="index"
            >{{ item.standardName }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="subjectId"
        >
          <Select
            v-model="formData.subjectId"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'subjectName')"
          >
            <Option
              v-for="(item,index) in subjectList"
              :value="item.schoolId"
              :key="index"
            >{{ item.customName }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="classLevel"
        >
          <Select
            v-model="formData.classLevel"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'classLevelName')"
          >
            <Option
              v-for="(item,index) in classLevelList"
              :value="item.schoolId"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem>
          <p style="margin-left: -100px;position: relative;height: 30px;">
            <Spin
              size="large"
              fix
              v-if="diagnosisScore.spining === 1"
            ></Spin>
            <span v-if="diagnosisScore.spining === 2 && !diagnosisScore.value"></span>
          </p>
        </FormItem>
        <FormItem
          label=""
          prop="classType"
        >
          <Select
            v-model="formData.classType"
            style="width: 150px;"
          >
            <Option
                v-for="item in classTypeList"
                :value="item.id"
                :key="item.id"
                :disabled="!$haveAuthority('roleRelativeData',classTypeRuleMap[item.id])"
              >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <template v-if="formData.classType===2||formData.classType===4">
          <FormItem
            label=""
            prop="serviceCenterId"
          >
            <Select
              v-model="formData.serviceCenterId"
              style="width: 150px;"
              clearable
              filterable
              :remote-method="q => getServiceCenterList(q, formData.cityCode)"
              label-in-value
              @on-change="handleSelectChange($event, 'serviceCenterName')"
            >
              <Option
                v-for="(item,index) in serviceCenterList"
                :value="item.schoolId"
                :key="index"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
        </template>
        <FormItem
          label=""
          prop="classtimeTimeClassifys"
        >
          <Select
            v-model="formData.classtimeTimeClassifys"
            style="width: 150px;"
            clearable
            multiple
          >
            <Option
              v-for="(item,index) in timeTypeList"
              :value="item.name"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="classtimeTimePeriods"
        >
          <Select
            v-model="formData.classtimeTimePeriods"
            style="width: 150px;"
            clearable
            multiple
          >
            <Option
              v-for="(item,index) in timeList"
              :value="item.name"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="intention"
        >
          <Select
            v-model="formData.intention"
            style="width: 150px;"
            clearable
          >
            <Option
              v-for="(item,index) in intentionList"
              :value="item"
              :key="index"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=""
          prop="recommendName"
        >
          <Input
            v-model="formData.recommendName"
            placeholder=""
            :maxlength="10"
            clearable
          />
        </FormItem>
        <FormItem
          label=""
          prop="recommendCard"
        >
          <Input
            v-model="formData.recommendCard"
            placeholder=""
            clearable
            :maxlength="20"
          />
        </FormItem>
        <br>
        <FormItem
          label=""
          prop="remark"
        >
          <Input
            style="width: 740px;"
            v-model="formData.remark"
            type="textarea"
            :autosize="{minRows: 3,maxRows: 10}"
            placeholder=""
            :maxlength="100"
          ></Input>
        </FormItem>
      </Form>
      <Row
        type="flex"
        justify="center"
        :gutter="20"
      >
        <Col>
        <Button
          type="primary"
          @click="confirm"
          :loading="confirmLoading"
        ></Button>
        </Col>
        <Col>
        <Button
          @click="$emit('close')"
          :loading="confirmLoading"
        ></Button>
        </Col>
      </Row>
    </template>

    <!-- type: 2  -->
    <template v-if="type === 2 || type === 3">
      <h3 style="margin-bottom: 20px;"></h3>
      <div class="record">
        <Row
          v-for="(item, index) in updateRecords"
          :key="index"
        >
          <Col span="24">: {{ item.logTypeName }}</Col>
          <Col span="6">: {{ item.year }}</Col>
          <Col span="6">: {{ item.termName}}</Col>
          <Col span="6">: {{ item.gradeName}}</Col>
          <Col span="6">: {{ item.subjectName }}</Col>
          <Col span="6">: {{ item.classLevelName }}</Col>
          <Col span="6">: {{ item.classTypeName }}</Col>
          <Col span="6">: {{ item.serviceCenterName }}</Col>
          <Col span="6">: {{ item.classtimeTimeClassifys }}</Col>
          <Col span="6">: {{ item.classtimeTimePeriods }}</Col>
          <Col span="6">: {{ item.intention }}</Col>
          <Col span="6">: {{ item.recommendName }}</Col>
          <Col span="6">: {{ item.recommendCard }}</Col>
          <Col span="24">: {{ item.remark }}</Col>
          <Col span="6">: {{ item.creatorName }}</Col>
          <Col span="6">: {{ item.createDate }}</Col>

          <Col span="24">
          <Divider style="margin: 10px 0;background-color: #333;"></Divider>
          </Col>
        </Row>
      </div>
    </template>

  </div>

</template>

<script>
import getUserInfoByUid from '@/components/mixin/getUserInfoByUid'
import baseDataSearchMixin from './baseDataSearchMixin'
import cuanbanMixin from './cuanbanMixin'
import { addAssembleClassDemand, editAssembleClassDemand, getAssembleClassDemandInfoById } from '@/api/assembleClass'
import { abilityStudentLevel } from '@/api/student'
export default {
  name: 'createCuanban',
  mixins: [getUserInfoByUid, baseDataSearchMixin, cuanbanMixin],
  props: {
    type: { // 1  2 3
      type: Number,
      default: 1
    },
    oldFormData: { // 
      type: Object,
      default: () => {
        return {}
      }
    },
    classTypeList: { // 
      type: Array,
      default: () => {
        return []
      }
    },
    classTypeRuleMap: { // map
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  watch: {
    'formData.classLevel': async function () {
      if (this.formData.classLevel && this.formData.gradeId && this.formData.subjectId) {
        const params = {
          levelDegree: this.formData.classLevel,
          subjectId: this.formData.subjectId,
          gradeId: this.formData.gradeId,
          uid: this.formData.uid,
          cityCode: this.formData.cityCode
        }
        this.diagnosisScore.spining = 1
        const data = await this.abilityStudentLevel(params)
        this.diagnosisScore.value = data
        this.diagnosisScore.spining = 2
      }
    }
  },
  data () {
    return {
      formData: {
        cityCode: '',
        year: new Date().getFullYear() + '', // , // 
        termId: '', // 
        termName: '', // 
        gradeId: '', // 
        gradeName: '', // 
        subjectId: '', // 
        subjectName: '', // 
        classLevel: '', // 
        classLevelName: '', // 
        serviceCenterId: '', // 
        serviceCenterName: '', // 
        classtimeTimeClassifys: '', // 
        classtimeTimePeriods: '', // 
        intention: '', // 
        name: '', // 
        uid: '', // uid
        studentCard: '', //  
        remark: '', // 
        recommendName: '', // 
        recommendCard: '', // 
        classType: 1// 
      },
      ruleValidate: {
        year: [
          { required: true, message: '', trigger: 'change' }
        ],
        termId: [
          { required: true, message: '', trigger: 'change' }
        ],
        gradeId: [
          { required: true, type: 'number', message: '', trigger: 'change' }
        ],
        subjectId: [
          { required: true, message: '', trigger: 'change' }
        ],
        classLevel: [
          { required: true, message: '', trigger: 'change' }
        ],
        serviceCenterId: [
          { required: true, message: '', trigger: 'change' }
        ],
        classtimeTimeClassifys: [
          { required: true, type: 'array', message: '', trigger: 'change' }
        ],
        classtimeTimePeriods: [
          { required: true, type: 'array', message: '', trigger: 'change' }
        ],
        classType: [
          { required: true, type: 'number', message: '', trigger: 'change' }
        ]
      },
      confirmLoading: false,
      updateRecords: [], // 
      diagnosisScore: {
        spining: 0,
        value: null
      }
    }
  },
  mounted () {
    if (this.type === 2 || this.type === 3) {
      Object.assign(this.formData, this.oldFormData)
      this.getAssembleClassDemandInfoById()
    }
    if (this.formData.cityCode) {
      this.initFormOptions()
    }
  },
  methods: {
    // 
    async abilityStudentLevel (params) {
      try {
        const data = await abilityStudentLevel(params)
        return data
      } catch (error) {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      }
    },
    // id
    async getAssembleClassDemandInfoById () {
      try {
        const params = {
          assembleClassDemandId: this.oldFormData.id,
          cityCode: this.oldFormData.cityCode
        }
        const res = await getAssembleClassDemandInfoById(params)
        let updateRecords = Object.freeze(res.logList)
        delete res.logList
        this.formData = Object.assign({}, res, {
          classtimeTimePeriods: res.classtimeTimePeriods.split(','),
          classtimeTimeClassifys: res.classtimeTimeClassifys.split(',')
        })
        this.serviceCenterList = [
          { schoolId: res.serviceCenterId, name: res.serviceCenterName }
        ]
        this.updateRecords = updateRecords
      } catch (error) {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      }
    },
    initFormOptions () {
      this.getBaseDataTermList()
      this.getYears()
      this.getSubjectList()
      this.getGradeList()
      this.getTimeList()
      this.getTimeTypeList()
      this.queryLevelDegreeList()
    },
    confirm () {
      this.$refs.formValidate.validate((valid, ...rest) => {
        if (valid) {
          if (this.type === 1) this.addAssembleClassDemand()
          else this.editAssembleClassDemand()
        }
      })
    },
    // 
    async addAssembleClassDemand () {
      try {
        this.confirmLoading = true
        const params = Object.assign({}, this.formData, {
          classtimeTimePeriods: this.formData.classtimeTimePeriods.join(','),
          classtimeTimeClassifys: this.formData.classtimeTimeClassifys.join(',')
        })
        if (params.classType === 1 || !params.classType) {
          delete params.serviceCenterId
        }
        await addAssembleClassDemand(params)
        setTimeout(() => {
          this.$Message.success({ content: '' })
          this.confirmLoading = false
          this.$emit('close')
          this.$emit('success')
        }, 2000)
      } catch (error) {
        this.confirmLoading = false
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      }
    },
    // 
    async editAssembleClassDemand () {
      try {
        this.confirmLoading = true
        const params = Object.assign({}, this.formData, {
          classtimeTimePeriods: this.formData.classtimeTimePeriods.join(','),
          classtimeTimeClassifys: this.formData.classtimeTimeClassifys.join(',')
        })
        if (params.classType === 1 || !params.classType) {
          delete params.serviceCenterId
        }
        await editAssembleClassDemand(params)
        setTimeout(() => {
          this.$Message.success({ content: '' })
          this.confirmLoading = false
          this.$emit('close')
          this.$emit('success')
        }, 2000)
      } catch (error) {
        this.confirmLoading = false
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      }
    },
    setStuInfo (stuInfo) {
      this.formData.cityCode = stuInfo.cityCode
      this.formData.uid = stuInfo.uid
      this.formData.name = stuInfo.realName
      this.formData.studentCard = stuInfo.card
      this.formData.gradeId = stuInfo.gradeId
      this.formData.gradeName = stuInfo.gradeName
      this.initFormOptions()
    }
  }
}
</script>
<style lang="less" scoped>
/deep/ .record .ivu-row .ivu-col {
  margin: 5px 0;
}
#TalScoreRecording {
  overflow-x: hidden;
  padding: 10px;
}

.stu-search {
  /deep/ .ivu-col {
    white-space: nowrap;
  }
}
</style>
