<template>
  <Modal
    v-model="showBatchImportWrapperModal"
    class="batchImportModal"
    title=""
    width="663"
    :mask-closable="false"
    :closable="false"
  >
    <div class="wrapper">
      <div class="cuanbanSettings">
        <Row>
          <Col span="5">
            <p style="marginTop: 16px; marginLeft: 10px"></p>
          </Col>
          <Col span="17">
            <Button
              type="primary"
              style="marginTop: 8px; marginBottom: 10px"
              :loading="downloading"
              @click="handleDownloadExcel"
            ></Button>
            <p style="marginTop: 3px">1.</p>
            <p style="marginTop: 3px">2.</p>
            <p style="marginTop: 3px">3.10</p>
          </Col>
        </Row>
      </div>
      <div class="importSettings">
        <Row>
          <Col span="5">
            <p style="marginTop: 32px; marginLeft: 10px"></p>
          </Col>
          <Col span="17">
            <div
              v-if="errorTargetData.length"
              class="error-box"
              style="marginTop: 25px;"
            >
              <!-- <Upload
                v-model="fileList"
                ref="upload"
                :required="true"
                platform="fe-service-platform"
              /> -->
              <TalOssUploadUi
                v-model="fileList"
                ref="upload"
                :required="true"
                btnName=""
                platform="fe-service-platform"
              />
              <p
                class="flex"
                style="justify-content: flex-start; align-items: center; marginTop: 16px"
              >
                <Icon
                  style="color: red; font-size: 20px; font-weight: bold; margin-right: 8px; margin-bottom: 8px"
                  type="md-close-circle"
                />
                <b style="margin-bottom: 6px"> {{errorTargetData.length}}</b>
              </p>
              <div v-for="(item, index) in errorTargetData" :key="index" style="marginTop: 16px">
                <div class="error-name" v-if="item.log">{{ item.message }}{{item.log.length}}</div>
                <ErrorTargetTable v-if="item.log" :data="item.log" />
              </div>
            </div>
            <div
              v-else
              style="marginTop: 25px;"
            >
              <!-- <Upload
                v-model="fileList"
                ref="upload"
                :required="true"
                platform="fe-service-platform"
              /> -->
              <TalOssUploadUi
                v-model="fileList"
                ref="upload"
                :required="true"
                btnName=""
                platform="fe-service-platform"
              />
            </div>
          </Col>
        </Row>
      </div>
    </div>
    <div slot="footer" style="justify-content: center">
      <template v-if="!errorTargetData.length">
        <Button :disabled="uploading" @click="closeUploadModal"></Button>
        <Button :loading="uploading" :disabled="!fileList.length" type="primary" @click="handleUploadTargetData"></Button>
      </template>
      <template v-else>
        <Button @click="closeUploadModal"></Button>
        <Button type="primary" @click="errorTargetData = []"></Button>
      </template>
    </div>
  </Modal>
</template>

<script>
// import FileUpload from './fileUpload'
import ErrorTargetTable from './errorTargetTable.vue'
// import Upload from './upload'
import { downloadExcel, uploadFile } from '@/api/assembleClass'
import util from '@/common/util'
import Cookie from 'js-cookie'
export default {
  name: 'batchImportWrapper',
  props: {
    cityCode: String
  },
  components: {
    ErrorTargetTable
    // Upload
  },
  data () {
    return {
      showBatchImportWrapperModal: false,
      errorTargetData: [],
      fileList: [], // 
      uploading: false,
      downloading: false// 
    }
  },
  methods: {
    //  
    batchImportWrapperHandler () {
      this.showBatchImportWrapperModal = true
    },
    //  
    closeUploadModal () {
      this.fileList = []
      this.errorTargetData = []
      this.$refs.upload.fileList = []
      this.showBatchImportWrapperModal = false
    },
    // 
    async handleUploadTargetData () {
      const fileList = this.fileList.filter((item) => item.status === 5) || []
      if (fileList.length === 0) {
        return this.$Message.warning({
          content: '',
          duration: 5
        })
      }
      if (fileList.length < this.fileList.length) {
        return this.$Message.warning({
          content: '',
          duration: 5
        })
      }
      const params = {
        fileUrl: fileList[0].path,
        cityCode: this.cityCode
      }
      try {
        this.uploading = true
        const data = await uploadFile(params)
        if (data.execResult === '2') {
          this.errorTargetData = data.messageData
        } else {
          this.closeUploadModal()
          this.$Message.success({
            content: '',
            duration: 5
          })
          this.showBatchImportWrapperModal = false
          this.$emit('updateTableData')
        }
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
      this.uploading = false
    },
    // 
    async handleDownloadExcel () {
      try {
        const params = {
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        }
        this.downloading = true
        const data = await downloadExcel(params)
        const fileName = '-.xlsx'
        util.downloadBolbFile(
          data,
          fileName,
          'application/vnd.ms-excel;charset=utf-8'
        )
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
      this.downloading = false
    }
  }
}
</script>

