<template>
  <!--  -->
  <div>
    <Form inline :label-width="80" >
      <FormItem label="">
        <Select v-model="formData.status" style="width: 150px;" clearable @on-change="search">
          <Option
            v-for="item in assessStatusMap"
            :value="item.value"
            :key="item.value"
          >{{ item.label }}</Option>
        </Select>
      </FormItem>
    </Form>

    <!--  -->
    <Table
      border
      ref="selection"
      :loading="loading"
      :columns="columns"
      :data="tableData"
      @on-expand="onExpand"
    ></Table>

    <!--  -->
    <div
      style="margin: 10px;"
      class="flex-page"
      v-show="!tableLoading"
    >
      <Page
        :total="formData.totalCount"
        :page-size="formData.pageSize"
        :current.sync="formData.pageNo"
        show-elevator
        show-sizer
        show-total
        transfer
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"
      ></Page>
    </div>
    <Modal
      title=""
      width='800'
      :footer-hide="true"
      v-model="searchStuModal">
      <TalStuSearch
        v-if="searchStuModal"
        ref="stuSearchForApply"
        :attendanceSearch='false'
        :showDetail='false'
        :showCrmInfo='false'
        choice-btn-text=''
        @stuInfo="setStuInfoForApplyHandle"></TalStuSearch>
    </Modal>
    <Modal
      title=""
      width='80%'
      :footer-hide="true"
      v-model="relatedModal">
      <Table
        border
        :columns="relatedCols"
        :data="relatedData"
      ></Table>
    </Modal>
    <!---->
    <TalApplyModal
      :show="applyShow"
      :classId="applyParams.classId"
      :stuId="applyParams.studentId"
      :stuCityCode="applyParams.cityCode"
      :creatorEmpNo="applyParams.empNo"
      @success="applySuccess"
      @close="applyShow = false">
    </TalApplyModal>

  </div>
</template>

<script>
import { queryPage, assembleRecordList, queryStudentDemandListByAssembleId, addSingleDemandRelate } from '@/api/assembleClass'
import Cookie from 'js-cookie'
import StuDetailExpand from './stuDetailExpand'
export default {
  name: 'CuanbanList',
  props: {
    parentFormData: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      loading: false,
      searchStuModal: false,
      formData: {
        status: '',
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      applyShow: false,
      applyParams: {
        classId: '',
        studentId: '',
        cityCode: '',
        empNo: ''
      },
      //   
      assessStatusMap: [
        {
          value: 1,
          label: ''
        },
        {
          value: 0,
          label: ''
        }
      ],
      columns: [
        {
          type: 'expand',
          width: 50,
          render: (h, params) => {
            return h(StuDetailExpand, {
              props: {
                stuList: params.row.stuList,
                loading: params.row.stuListLoading
              },
              on: {
                apply: row => {
                  this.detailRow = params.row
                  this.applyParams.classId = row.classId
                  this.applyParams.studentId = row.studentId
                  this.applyParams.cityCode = row.areaCode
                  this.applyShow = true
                }
              }
            })
          }
        },
        {
          title: '',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                on: {
                  click: () => {
                    this.detailRow = params.row
                    this.applyParams.classId = params.row.clazzDetail.classId
                    this.assembleId = params.row.id
                    this.searchStuModal = true
                  }
                }
              }, ''),
              h('br'),
              h('a', {
                on: {
                  click: () => {
                    const row = this.tableData[params.index]
                    this.$set(row, '_expanded', !row._expanded)
                    if (row._expanded) {
                      this.queryStudents(row)
                    }
                  }
                }
              }, this.tableData[params.index]._expanded ? '' : '')
            ])
          }
        },
        {
          title: '',
          key: 'className',
          align: 'center',
          render: (h, params) => {
            return params.row.clazzDetail.className ? h('a', {
              on: {
                click: () => {
                  this.$emit('showClassDetail', params.row.clazzDetail.classId)
                }
              }
            }, params.row.clazzDetail.className) : h('span', '--')
          }
        },
        {
          title: '',
          key: 'categoryTypeName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.categoryTypeName)
          }
        },
        {
          title: '',
          key: 'termName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.termName)
          }
        },
        {
          title: '',
          key: 'subjectName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.subjectName)
          }
        },
        {
          title: '',
          key: 'teacherNames',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.clazzDetail.teachers.map(t => {
              return h('a', {
                on: {
                  click: () => {
                    this.$emit('showTeacherDetail', params.row.cityCode, t.teacherId, t.teacherAccountType)
                  }
                }
              }, t.teacherName)
            }))
          }
        }, {
          title: '',
          key: 'startDate',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.startDate)
          }
        }, {
          title: '',
          key: 'endDate',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.endDate)
          }
        }, {
          title: '',
          key: 'classTimeDisplay',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.classTimeDisplay)
          }
        }, {
          title: '',
          key: 'serviceCenterName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.classroomServiceCenterName)
          }
        }, {
          title: '',
          key: 'classroomName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.classroomName)
          }
        }, {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.passedCount + '/' + params.row.clazzDetail.classCount)
          }
        }, {
          title: '',
          key: 'maxPersons',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.maxPersons)
          }
        }, {
          title: '',
          key: 'registPersons',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.registPersons)
          }
        }, {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.maxPersons - params.row.clazzDetail.registPersons)
          }
        }, {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('span', String(params.row.clazzDetail.isClose) === '1' ? '' : '')
          }
        }
      ],
      tableLoading: false,
      tableData: [],
      detailRow: null,
      relatedModal: false,
      assembleStatusMap: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: '',
        7: ''
      },
      relatedCols: [
        {
          title: '',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  addSingleDemandRelate({
                    assembleClassDemandId: params.row.id,
                    assembleId: this.assembleId
                  }).then(() => {
                    this.$Message.success('')
                    this.relatedModal = false
                    this.applyShow = true
                  }).catch(err => {
                    this.$Message.error({
                      content: '' + err.message,
                      duration: 5
                    })
                  })
                }
              }
            }, '')
          }
        },
        {
          title: '',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', this.assembleStatusMap[params.row.status])
          }
        },
        {
          title: '',
          key: 'name',
          align: 'center'
        },
        {
          title: '',
          key: 'receptionAdvisorEmpName',
          align: 'center'
        },
        {
          title: '',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '',
          key: 'creatorName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.creatorName || '')
          }
        },
        {
          title: '',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '',
          key: 'recommendName',
          align: 'center'
        },
        {
          title: '',
          key: 'year',
          align: 'center'
        },
        {
          title: '',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '',
          key: 'termName',
          align: 'center'
        },
        {
          title: '',
          key: 'classLevelName',
          align: 'center'
        },
        {
          title: '',
          key: 'serviceCenterName',
          align: 'center'
        },
        {
          title: '',
          key: 'classtimeTimeClassifys',
          align: 'center'
        },
        {
          title: '',
          key: 'classtimeTimePeriods',
          align: 'center'
        },
        {
          title: '',
          key: 'intention',
          align: 'center'
        }
      ],
      relatedData: []
    }
  },
  methods: {
    setStuInfoForApplyHandle (data) {
      this.searchStuModal = false
      this.applyParams.studentId = data.studentId
      this.applyParams.cityCode = data.cityCode
      this.showRelatedAssembleClass(data.uid)
      // this.applyShow = true
    },
    showRelatedAssembleClass (uid) {
      queryStudentDemandListByAssembleId({
        uid,
        assembleId: this.assembleId
      }).then(data => { // 
        if (data && data.length) {
          this.relatedData = data
          this.relatedModal = true
        } else {
          this.applyShow = true
        }
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      })
    },
    async getTableData () {
      this.loading = true
      const params = Object.assign({}, this.parentFormData, this.formData, {
        startCreateDate: '',
        endCreateDate: ''
      })
      if (params.classType === 1 || !params.classType) {
        delete params.serviceCenterId
      }
      try {
        const data = await queryPage(params)
        this.tableData = data.records.map(d => {
          d.stuList = []
          return d
        })
        this.formData.totalCount = data.totalCount
      } catch (e) {
        this.$Message.error({
          content: '' + e.message,
          duration: 5
        })
      } finally {
        this.loading = false
      }
    },
    // 
    changePage (num) {
      this.formData.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    search () {
      this.formData.pageNo = 1
      this.getTableData()
    },
    async applySuccess () {
      try {
        // await this.getTableData()
        const row = this.tableData.find(d => d.id === this.detailRow.id)
        this.queryStudents(row)
        this.$set(row, '_expanded', true)
      } catch (e) {
        this.$Message.error({
          content: '' + e.message || e,
          duration: 5
        })
      }
    },
    onExpand (row, status) {
      const expandRow = this.tableData.find(d => d.id === row.id)
      this.$set(expandRow, '_expanded', status)
      if (status) {
        this.queryStudents(expandRow)
      }
    },
    queryStudents (row) {
      row.stuListLoading = true
      assembleRecordList({
        id: row.id,
        classId: row.clazzDetail.classId,
        cityCode: row.cityCode
      }).then(data => {
        row.stuList = data
      }).catch(err => {
        this.$Message.error({
          content: '' + err.message,
          duration: 5
        })
      }).finally(() => {
        row.stuListLoading = false
      })
    }
  },
  mounted () {
    this.applyParams.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
  }
}
</script>

<style lang="less" scoped>
.flex-page {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>
