<template>
  <div class="wholeSearch">
    <div class="formLayout">
      <Form
        class="form-container"
        :model="formData"
        :label-width="100"
        inline
      >
        <FormItem label="">
          <Select
            class="iptWidth"
            v-model="formData.cityCode"
            filterable
            @on-change="initCityList"
          >
            <Option v-for="(option,index) in cityList" :value="option.id" :key="index">{{option.name}}</Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Input
            placeholder="///"
            v-model="formData.name"
            class="iptWidth">
          </Input>
        </FormItem>
        <FormItem label="">
          <Select
            class="iptWidth"
            v-model="formData.productType"
            clearable
          >
            <Option :value="'1'"></Option>
            <Option :value="'2'"></Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            class="iptWidth"
            v-model="formData.categoryType"
            clearable
          >
            <Option :value="1"></Option>
            <Option :value="2"></Option>
            <Option :value="4"></Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            clearable
            filterable
            v-model="formData.serviceCenterId"
            @on-change="changeService"
            :remote-method="q => getServiceCenterList(q, formData.cityCode)"
            class="iptWidth"
          >
            <Option
              v-for="(item,index) in serviceCenterList"
              :key="index"
              :value="item.schoolId"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            clearable
            filterable
            v-model="formData.teachingPointCode"
            :disabled="!formData.serviceCenterId"
            class="iptWidth"
          >
            <Option
              v-for="(item,index) in venueList"
              :key="index"
              :value="item.schoolId"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            filterable
            clearable
            label-in-value
            placeholder=""
            :loading="loadingTeacherName"
            :remote-method="remoteListTeacherName"
            v-model="formData.teacherEmpNo"
            class="iptWidth"
          >
            <Option
              v-for="(item, index) in teacherNameList"
              :value="item.empNo"
              :label="item.realName"
              :key="index"
            >
              <div slot>{{`${item.realName} (${item.empNo})`}}</div>
            </Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            class="iptWidth"
            v-model="formData.classTimeTypeName"
            clearable
          >
            <Option
              v-for="(item, index) in timeTypeList"
              :key="index"
              :value="item"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem label="">
          <Select
            class="iptWidth"
            v-model="formData.period"
            clearable
          >
            <Option
              v-for="item in timeList"
              :key="item.id"
              :value="item.name"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem class="btnArea">
          <Button
            class="searchBtn"
            type="primary"
            @click="assembleSearch"
          ></Button>
          <Button
            type="default"
            @click="resetField"
          ></Button>
        </FormItem>
      </Form>
    </div>
    <div class="cardWrapper">
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(1)"
        :class="{'active': activeCard === 1}"
      >
        <p class="typeName">
          
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p></p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.stayOnlineTotal}}</div>
      </div>
      <div
        ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(2)"
        :class="{'active': activeCard === 2}"
      >
        <p class="typeName">
          
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p></p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.assembleInTotal}}</div>
      </div>
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(3)"
        :class="{'active': activeCard === 3}"
      >
        <p class="typeName">
          
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p></p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.assembleSuccessTotal}}</div>
      </div>
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(4)"
        :class="{'active': activeCard === 4}"
      >
        <p class="typeName">
          
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p></p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.openClassSuccess}}</div>
      </div>
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(5)"
        :class="{'active': activeCard === 5}"
      >
        <p class="typeName">
          
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p></p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.assembleFail}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { queryServiceCenterByEmpNo } from '@/api/clazz'
import { getTotalList } from '@/api/assembleClass'
import BaseDataSearchMixin from './baseDataSearchMixin'
// import { mapState } from 'vuex'
import Cookie from 'js-cookie'
export default {
  name: 'classSearch',
  mixins: [BaseDataSearchMixin],
  data () {
    return {
      cityList: [], // 
      hover: false,
      hover2: false,
      activeCard: 1, //  1-
      stayOnlineTotal: 0, // 1-
      assembleInTotal: 0, // 2-
      assembleSuccessTotal: 0, // 3-
      openClassSuccess: 0, // 4-
      assembleFail: 0, // 5-
      formData: {
        name: '', //  
        categoryType: '', // 
        productType: '', // 
        serviceCenterId: '', // ID
        teachingPointCode: '', // ID
        teacherEmpNo: '', // 
        classTimeTypeId: '', // Id  - 5
        classTimeTypeName: '', // 
        period: '', // 
        cityCode: '', // 
        queryType: 1 //   1- 2- 3- 4- 5- 1-
        // pageSize: 10, //  
        // pageNo: 1 //  
      },
      serviceCenterList: [], // 
      venueList: [], // 
      teacherNameList: [], // 
      loadingTeacherName: false, // 
      timeTypeList: ['', '', '', '', '', '', ''], // ()
      classTimeTypeIdMap: { // ID
        '': 1,
        '': 2,
        '': 3,
        '': 4,
        '': 5,
        '': 6,
        '': 7
      },
      timeList: [] // 
    }
  },
  async mounted () {
    await this.getCityListHandler(data => {
      this.formData.cityCode = data[0].id
    })
    this.initCityList()
  },
  methods: {

    // 
    async initCityList () {
      try {
        await this.getCurrentserviceCenter()
        this.assembleSearch()
        this.getTimeList()
        if (this.formData.serviceCenterId) {
          this.getVenueListHandler({ cityCode: this.formData.cityCode, serviceId: this.formData.serviceCenterId }).then(res => {
            this.venueList = res
          }).catch(err => {
            this.$Message.warning(':' + err.message)
          })
        }
      } catch (error) {
        this.$Message.error({ content: ': ' + error.message, duration: 5 })
      }
    },
    // 
    getAssembleTotal () {
      let formData = {
        ...this.formData,
        cityCode: this.formData.cityCode
      }
      getTotalList(formData).then(data => {
        if (data) {
          this.stayOnlineTotal = data.stayOnlineTotal
          this.assembleInTotal = data.assembleInTotal
          this.assembleSuccessTotal = data.assembleSuccessTotal
          this.openClassSuccess = data.openClassSuccess
          this.assembleFail = data.assembleFail
        } else {
          this.stayOnlineTotal = 0
          this.assembleInTotal = 0
          this.assembleSuccessTotal = 0
          this.openClassSuccess = 0
          this.assembleFail = 0
        }
      }).catch(err => {
        this.$Message.error({ content: ': ' + err.message, duration: 5 })
        this.stayOnlineTotal = 0
        this.assembleInTotal = 0
        this.assembleSuccessTotal = 0
        this.openClassSuccess = 0
        this.assembleFail = 0
      })
    },
    // 
    async getCurrentserviceCenter () {
      try {
        const data = await queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        })
        if (data.serviceCenterId && data.serviceCenterName) {
          this.serviceCenterList = [
            { schoolId: data.serviceCenterId, name: data.serviceCenterName }
          ]
          this.formData.serviceCenterId = data.serviceCenterId
        }
      } catch (error) {
        this.$Message.error({ content: ': ' + error.message, duration: 5 })
      }
    },
    // 
    changeService (val) {
      this.venueList = []
      this.formData.teachingPointCode = ''
      if (!val) {
        return false
      }
      const req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning(':' + err.message)
      })
    },
    // 
    remoteListTeacherName (name) {
      clearTimeout(this.lazyTimer)
      this.lazyTimer = setTimeout(() => {
        this.getTeacher(name)
      }, 400)
    },
    // 
    getTeacher (query) {
      query = query.trim()
      this.loadingTeacherName = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.categoryType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        if (res.records && res.records.length) {
          this.teacherNameList = res.records
        }
        this.loadingTeacherName = false
      }).catch(err => {
        this.loadingTeacherName = false
        this.$Message.error(':' + err.message)
      })
    },
    // 
    async assembleSearch (isClearPageSize = false) {
      this.formData.classTimeTypeId = this.classTimeTypeIdMap[this.formData.classTimeTypeName]
      this.getAssembleTotal(this.formData)
      // this.getPage()
      this.$emit('queryParams', this.formData, new Date(), isClearPageSize)
    },
    // 
    // getPage () {
    //   this.formData.classTimeTypeId = this.classTimeTypeIdMap[this.formData.classTimeTypeName]
    //   const params = this.formData
    //   getAssemblePage(params).then(res => {
    //     if (res) {
    //       this.tableData = res
    //     } else {
    //       this.tableData = {}
    //     }
    //   }).catch(error => {
    //     this.tableData = {}
    //     this.$Message.error({ content: ': ' + error.message, duration: 5 })
    //   })
    // },
    // 
    resetField () {
      this.formData.name = '' //  
      this.formData.categoryType = '' // 
      this.formData.teachingPointCode = '' // ID
      this.formData.teacherEmpNo = '' // ID
      this.formData.classTimeTypeId = '' // ()
      this.formData.classTimeTypeName = '' // ()
      this.formData.period = '' // 
      this.formData.queryType = 1 // 1-
      this.activeCard = 1 // 1-
      this.assembleSearch(true)
    },
    // 
    handleCardClick (index) {
      this.activeCard = index
      this.formData.queryType = index
      this.$emit('queryParams', this.formData, new Date(), true)
    }
  }
}
</script>

