import axios from '@/libs/base.request'

/**
 * 
 * @param params
 * @returns {*}
 */
export function combineList (params) {
  return axios.request({
    url: '/MASTER-BD/bd-web/baseData/combineList',
    method: 'post',
    data: params
  })
}

