<template>
  <!--  -->
  <div>
    <Form :label-width="100">
      <FormItem label="" required>
        <Cascader style="width: 200px;display: inline-block;" :data="cascaderList" v-model="cascaderValue"></Cascader>
      </FormItem>
      <FormItem label="" required v-if="nextTimeShow">
        <DatePicker v-model="formData.nextTime" @on-change="timeChange" type="datetime" format="yyyy-MM-dd HH:mm" placeholder="" style="width: 200px"></DatePicker>
      </FormItem>
      <FormItem label="" required>
        <Input v-model="formData.remark" placeholder="" type="textarea" :minlength="5" style="width: 80%;" />
      </FormItem>
    </Form>
    <Row type="flex" justify="center" :gutter="20">
      <Col><Button type="primary" @click="submit"></Button></Col>
      <Col><Button @click="$emit('close')"></Button></Col>
    </Row>
    <h3 style="margin: 16px 0" v-if="applyChangedRecord.length"></h3>
    <Row style="padding-left: 24px;" v-for="(item, index) in applyChangedRecord" :key="index">
       <!-- - {{applySecondStatusMap[item.applySecondStatus]}} -->
      <Col>: {{ applyStatusMap[item.currentApplyStatus] }}</Col>
      <Col>: {{ item.remark }}</Col>
      <Col span="8">: {{ item.modifyEmpName }}</Col>
      <Col span="16">: {{ item.createTime }}</Col>
      <Col span="24"><Divider /></Col>
    </Row>
  </div>
</template>

<script>
import { modifyApplyStatus, queryModifyRecords } from '@/api/appointmentApply'
export default {
  name: 'changeApplyStatus',
  props: {
    applyInfo: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      nextTime: '',
      nextTimeShow: false, // 
      formData: {
        remark: '', // 
        applyType: 1, // 1: 
        preApplyStatus: 0, // 
        nextFollowUpTime: '' // 
      },
      applyStatusMap: {
        0: '',
        // 1: '',
        2: '',
        3: ''
      },
      applySecondStatusMap: {
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: ''
      },
      applyChangedRecord: [],
      cascaderValue: [], // 
      // 
      cascaderList: [
        // {
        //   value: 1,
        //   label: ''
        // },
        {
          value: 3,
          label: '',
          children: [
            {
              value: 1,
              label: ''
            },
            {
              value: 2,
              label: ''
            }
          ]
        },
        {
          value: 2,
          label: '',
          children: [
            {
              value: 3,
              label: ''
            },
            {
              value: 4,
              label: ''
            },
            {
              value: 5,
              label: ''
            },
            {
              value: 6,
              label: '',
              children: [
                {
                  value: 7,
                  label: ''
                },
                {
                  value: 8,
                  label: ''
                }
              ]
            }
          ]
        }
      ]
    }
  },
  watch: {
    cascaderValue (val) {
      if (val && val.length && val[0] === 3) { // 
        this.nextTimeShow = true
      } else {
        this.nextTimeShow = false
        this.nextTime = ''
        this.formData.nextFollowUpTime = ''
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.formData.preApplyStatus = this.applyInfo.applyStatus

      this.cascaderValue = [
        this.applyInfo.applyStatus,
        this.applyInfo.applySecondStatus,
        this.applyInfo.applyThirdStatus
      ]
      this.formData.applyId = this.applyInfo.id
      this.queryModifyRecords()
    })
  },
  methods: {
    timeChange (time) {
      this.formData.nextFollowUpTime = time
    },
    async submit () {
      if (!this.cascaderValue[0]) {
        return this.$Message.warning('')
      }
      if (!this.formData.remark.trim()) {
        return this.$Message.warning('')
      }

      const currentApplyStatus = this.cascaderValue[0] // 
      const applySecondStatus = this.cascaderValue[1] || null // 
      const applyThirdStatus = this.cascaderValue[2] || null // 
      if (currentApplyStatus === 3 && !this.formData.nextFollowUpTime) { // 
        return this.$Message.warning('')
      }
      const params = {
        ...this.formData,
        currentApplyStatus,
        applySecondStatus,
        applyThirdStatus
      }

      try {
        await modifyApplyStatus(params)
        this.$Message.success({ content: `` })
        this.$emit('success')
        this.$emit('close')
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    },
    async queryModifyRecords () {
      try {
        const params = { applyId: this.formData.applyId }
        const data = await queryModifyRecords(params)
        this.applyChangedRecord = Object.freeze(data)
      } catch (error) {
        this.$Message.error({
          content: `: ${error.message}`,
          duration: 5
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .ivu-col {
  line-height: 2;
}
</style>
