<template>
    <!--  -->
    <div>
        <p class="mgb-24">
            <span class="mgl-30">: {{auditionInfo.className || '--'}}</span>
            <span class="mgl-30">: {{auditionInfo.curriculumNum || '--'}}</span>
            <span class="mgl-30">: {{auditionInfo.teacherName || '--'}}</span>
        </p>
        <p class="mgb-24">
            <span class="mgl-30">: {{auditionInfo.classTimeVal || '--'}}</span>
            <span class="mgl-30">: {{auditionInfo.serviceCenter || '--'}}</span>
            <span class="mgl-30">: {{auditionInfo.classroom || '--'}}</span>
        </p>
        <Form :label-width="95" inline>
            <FormItem label=": ">
                <RadioGroup v-model="formValidate.isAttend">
                    <Radio :label="1"></Radio>
                    <Radio :label="2"></Radio>
                    <Radio :label="0"></Radio>
                </RadioGroup>
            </FormItem>
            <div></div>
            <FormItem label="ITS: ">{{ auditionInfo.isItsAttendance === 1 ? '' : '' }}</FormItem>
            <FormItem label=": ">{{ auditionInfo.attendanceTime || '--' }}</FormItem>
            <FormItem label=": ">
                <DatePicker
                type="datetime" placeholder="" style="width: 200px;"
                format="yyyy-MM-dd HH:mm:ss"
                v-model="formValidate.nextFollowTimeVal"
                :options="dateOptions"
                @on-change="dateChange($event, 'nextFollowTime')"
                ></DatePicker>
            </FormItem>
            <FormItem label=": " required>
                <Input v-model="formValidate.reason" type="textarea" placeholder="" style="width: 300px" />
            </FormItem>
        </Form>
        <Row type="flex" justify="center" :gutter="20">
            <Col>
                <Button type="primary" @click="confirm" :loading="btnLoading"></Button>
            </Col>
            <Col>
                <Button @click="cancel"></Button>
            </Col>
        </Row>
    </div>
</template>

<script>

import { modifyAttendance } from '@/api/audition'

export default {
  name: 'ChangeAttendance',
  props: {
    auditionInfo: {
      // 
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      formValidate: {
        nextFollowTime: '', // 
        nextFollowTimeVal: '', // 
        reason: '', // 
        isAttend: 1 // 1  2 0
      },
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      btnLoading: false
    }
  },
  mounted () {
    this.formValidate.isAttend = this.auditionInfo.isAttendance || 0
  },
  methods: {
    dateChange (val, typeName) {
      this.formValidate[typeName] = val
    },
    async confirm () {
      if (!this.formValidate.reason) {
        this.$Message.warning({ content: '' })
        return
      }
      try {
        const params = {
          ...this.formValidate,
          appointmentId: this.auditionInfo.id,
          auditionId: this.auditionInfo.auditionId,
          studentUid: this.auditionInfo.uid,
          curriculumNum: this.auditionInfo.curriculumNum,
          classId: this.auditionInfo.classId
        }
        this.btnLoading = true
        await modifyAttendance(params)
        this.btnLoading = false
        this.$emit('success')
        this.$emit('cancel')
      } catch (error) {
        this.btnLoading = false
        this.$Message.error({
          content: ': ' + error.message,
          duration: 5
        })
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="less" scoped>
.mgl-30 {
  margin-left: 30px;
}

.mgb-24 {
  margin-bottom: 24px;
}
</style>
