<template>
    <!--  -->
    <div>
        <Form :label-width="120">
            <FormItem label=": ">
                <DatePicker
                type="datetime" placeholder="" style="width: 200px;"
                format="yyyy-MM-dd HH:mm:ss"
                v-model="formValidate.nextFollowTimeVal"
                :options="dateOptions"
                @on-change="dateChange($event, 'nextFollowTime')"
                ></DatePicker>
            </FormItem>
            <FormItem label=": " required>
                <Select v-model="formValidate.cancelReason" placeholder="" style="width: 200px;" @on-change="onReasonChange">
                    <Option :value="item.id" v-for="(item, index) in cancelReasons" :key="index">{{ item.label }}</Option>
                </Select>
            </FormItem>
            <FormItem label=" " v-if="isShowOtherReason">
                <Input type="textarea" v-model="formValidate.cancelReasonExtra" placeholder="" style="width: 200px" />
            </FormItem>
        </Form>
        <Row type="flex" justify="center" :gutter="20">
            <Col>
                <Button @click="cancel"></Button>
            </Col>
            <Col>
                <Button type="primary" @click="confirm" :loading="btnLoading"></Button>
            </Col>
        </Row>
    </div>
</template>

<script>
import { cancel } from '@/api/appointment'
export default {
  name: 'CancelAudition',
  props: {
    businessId: { // id
      type: [String, Number],
      default: ''
    },
    appointmentId: { // id
      type: [String, Number],
      default: ''
    },
    operationType: { //  1: 
      type: Number,
      default: null
    },
    pyUid: { // uid
      type: [String, Number],
      default: ''
    },
    cityCode: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      formValidate: {
        nextFollowTime: '', // 
        nextFollowTimeVal: '', // 
        cancelReason: '', // 
        cancelReasonExtra: '' // 
      },
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      btnLoading: false,
      isShowOtherReason: false, // 
      cancelReasons: [
        { id: 1, label: '' },
        { id: 2, label: '' },
        { id: 3, label: '' },
        { id: 4, label: '' },
        { id: 5, label: '' },
        { id: 6, label: '' }
      ]
    }
  },
  methods: {
    onReasonChange (data) {
      if (data === 6) {
        this.isShowOtherReason = true
      } else {
        this.isShowOtherReason = false
      }
    },
    dateChange (val, typeName) {
      this.formValidate[typeName] = val
    },
    async confirm () {
      if (!this.formValidate.cancelReason) {
        this.$Message.warning({ content: '' })
        return
      }

      if (this.formValidate.cancelReason === 6 && !this.formValidate.cancelReasonExtra.trim()) {
        this.$Message.warning({ content: '' })
        return
      }
      const params = {
        ...this.formValidate,
        businessId: this.businessId,
        appointmentId: this.appointmentId,
        operationType: this.operationType,
        cancelType: this.operationType,
        pyUid: this.pyUid,
        cityCode: this.cityCode
      }

      try {
        this.btnLoading = true
        await cancel(params)
        setTimeout(() => {
          this.btnLoading = false
          this.$Message.success({ content: `` })
          this.$emit('success')
          this.cancel()
        }, 2000)
      } catch (error) {
        this.btnLoading = false
        this.$Message.error({ content: `: ${error.message}` })
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>
