<template>
  <!--  -->
  <div>
    <h3 style="margin-bottom: 20px;"></h3>
    <Form
      inline
      :label-width="90"
      ref="formValidate"
      :model="formData"
      :rules="ruleValidate"
    >
      <FormItem
        label=""
        prop="year"
      >
        <Select
          v-model="formData.year"
          style="width: 150px;"
          clearable
          :disabled="type !== 1"
        >
          <Option
            v-for="(item,index) in yearList"
            :value="item"
            :key="index"
          >{{ item }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label=""
        prop="termId"
      >
        <Select
          v-model="formData.termId"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'termName')"
        >
          <Option
            v-for="(item,index) in termList.slice(0, 4)"
            :value="item.standardId"
            :key="index"
          >{{ item.standardName }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label=""
        prop="gradeId"
      >
        <Select
          v-model="formData.gradeId"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'gradeName')"
        >
          <Option
            v-for="(item,index) in gradeList"
            :value="item.standardId"
            :key="index"
          >{{ item.standardName }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label=""
        prop="subjectId"
      >
        <Select
          v-model="formData.subjectId"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'subjectName')"
        >
          <Option
            v-for="(item,index) in subjectList"
            :value="item.schoolId"
            :key="index"
          >{{ item.customName }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label=""
        prop="classLevel"
      >
        <Select
          v-model="formData.classLevel"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'classLevelName')"
        >
          <Option
            v-for="(item,index) in classLevelList"
            :value="item.schoolId"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem
          label=""
          prop="classType"
      >
        <Select
          v-model="formData.classType"
          style="width: 150px;"
          :disabled="type !== 1"
        >
          <Option
              v-for="item in classTypeList"
              :value="item.id"
              :key="item.id"
              :disabled="!$haveAuthority('roleRelativeData',classTypeRuleMap[item.id])"
            >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <template v-if="formData.classType===2||formData.classType===4">
        <FormItem
        label=""
        prop="serviceCenterId"
        >
          <Select
            v-model="formData.serviceCenterId"
            style="width: 150px;"
            clearable
            filterable
            :remote-method="q => getServiceCenterList(q, formData.cityCode)"
            label-in-value
            :disabled="type !== 1"
            @on-change="handleSelectChange($event, 'serviceCenterName')"
          >
            <Option
              v-for="(item,index) in serviceCenterList"
              :value="item.schoolId"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
      </template>
      <FormItem
        label=""
        prop="classtimeTimeClassify"
      >
        <Select
          v-model="formData.classtimeTimeClassify"
          style="width: 150px;"
          clearable
          :disabled="type !== 1"
        >
          <Option
            v-for="(item,index) in timeTypeList"
            :value="item.name"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label=""
        prop="classtimeTimePeriod"
      >
        <Select
          v-model="formData.classtimeTimePeriod"
          style="width: 150px;"
          clearable
          :disabled="type !== 1"
        >
          <Option
            v-for="(item,index) in timeList"
            :value="item.name"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
    </Form>
    <h3>
      
      <span style="color: red;"></span>
    </h3>
    <h4 style="margin-top: 20px;margin-bottom: 10px;">: <span style="color: red;">{{ studentDemandListSorted.length }}</span></h4>
    <Table
      border
      :columns="columns"
      :data="studentDemandListSorted"
      :loading="tableLoading"
    ></Table>

    <div
      v-if="type !== 1"
      style="margin: 20px 0;padding-left: 40px;"
    >
      <div
        v-for="(item, index) in assessLogs"
        :key="index"
        style="margin-bottom: 10px;"
      >
        <template v-if="item.logType === 'add'">
          <h3 style="margin-left: -20px;margin-bottom: 10px;"></h3>
          <Row>
            <Col span="4">: {{ item.creatorName || '--' }}</Col>
            <Col span="4">: {{ item.creator || '--' }}</Col>
            <Col
              span="16"
              style="margin-bottom: 10px;"
            >: {{ item.createDate }}</Col>
            <Col span="24">: {{ item.remark || '--'}}</Col>
          </Row>
        </template>
        <template v-else>
          <h3 style="margin: 20px 0 10px -20px;"></h3>
          <Row>
            <Col span="4">: {{ item.creatorName || '--' }}</Col>
            <Col span="4">: {{ item.creator || '--' }}</Col>
            <Col
              span="16"
              style="margin-bottom: 10px;"
            >: {{ item.createDate }}</Col>
            <Col
              span="4"
              style="margin-bottom: 10px;"
            >: {{ item.logType === 'success' ? '' : item.logType === 'fail' ? '' : '--' }}</Col>
            <Col span="16">ID: {{ item.assembleClassId || '--' }}</Col>
            <Col span="24">{{ item.logType === 'success' ? '': ''}}: {{ item.approvalRemark || '--'}}</Col>
          </Row>
        </template>
      </div>
    </div>

    <Form
      inline
      :label-width="90"
      ref="formValidate2"
      :model="formData"
      :rules="ruleValidate"
      style="margin: 20px 0;"
    >

      <FormItem
        label=""
        prop="receptionEmpNo"
        v-if="type === 1"
      >
        <Select
          v-model="formData.receptionEmpNo"
          filterable
          clearable
          remote
          :remote-method="q => getAdvisorEmpNos(q, formData.cityCode)"
          label-in-value
          style="width: 150px;"
          @on-change="handleSelectChange($event, 'receptionEmpName')"
        >
          <Option
            v-for="item in advisorEmpNos"
            :value="item.empNo"
            :key="item.empNo"
          >{{item.name}}-{{item.empNo}}</Option>
        </Select>
      </FormItem>
      <!--  -->
      <template v-if="type === 3">
        <FormItem
          label=""
          prop="status"
        >
          <Select
            v-model="formData.status"
            style="width: 150px;"
            @on-change="handleStatusChange"
          >
            <Option
              v-for="(item,index) in openClassStatusList"
              :value="item.value"
              :key="index"
            >{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="ID"
          prop="classId"
          v-if="formData.status === 1"
        >
          <Input
            v-model="formData.classId"
            clearable
          />
        </FormItem>

      </template>
      <!-- - -->
      <FormItem
        label=""
        v-if="(type === 1 || type === 3) && formData.status === 1"
        prop="remark"
        class="disabled-required"
      >
        <Input
          style="width: 740px;"
          v-model="formData.remark"
          type="textarea"
          :autosize="{minRows: 3,maxRows: 10}"
          placeholder=""
          :maxlength="200"
        ></Input>
      </FormItem>
      <!-- - -->
      <FormItem
        label=""
        v-if="(type === 1 || type === 3) && formData.status === 2"
        prop="failReason"
      >
        <Input
          style="width: 740px;"
          v-model="formData.failReason"
          type="textarea"
          :autosize="{minRows: 3,maxRows: 10}"
          placeholder=""
          :maxlength="200"
        ></Input>
      </FormItem>
    </Form>

    <Row
      type="flex"
      justify="center"
      :gutter="20"
      v-if="type ===1 || type === 3"
    >
      <Col>
      <Button
        type="primary"
        @click="confirm"
        :loading="confirmLoading"
      >{{ type === 1 ? '': type === 3? '': ''}}</Button>
      </Col>
      <Col>
      <Button
        :loading="confirmLoading"
        @click="$emit('close')"
      ></Button>
      </Col>
    </Row>
  </div>
</template>

<script>
import { TalDecodeText } from '@peiyou/tal-element'
import baseDataSearchMixin from './baseDataSearchMixin'
import GroupMultiple from '@/utils/group'
import cuanbanMixin from './cuanbanMixin'
import { submit, queryDetailById, operate } from '@/api/assembleClass'
export default {
  name: 'OpenClassAeeseeApproval',
  mixins: [baseDataSearchMixin, cuanbanMixin],
  props: {
    parentFormData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    studentDemandList: { // 
      type: Array,
      default: () => {
        return []
      }
    },
    type: { //  1 2 3
      type: Number,
      default: 1
    },
    // ID
    accessId: {
      type: String,
      default: ''
    },
    classTypeList: { // 
      type: Array,
      default: () => {
        return []
      }
    },
    classTypeRuleMap: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      formData: {
        cityCode: '',
        year: new Date().getFullYear() + '', // , // 
        termId: '', // 
        termName: '',
        gradeId: '', // 
        gradeName: '', // 
        subjectId: '', // 
        subjectName: '', // 
        classLevel: '', // 
        classLevelName: '', // 
        serviceCenterId: '', // 
        serviceCenterName: '', // 
        classtimeTimeClassify: '', // 
        classtimeTimePeriod: '', // 
        receptionEmpNo: '', // 
        receptionEmpName: '', // 
        classId: '', // ID
        status: null, //  1 2 
        remark: '', // 
        failReason: '', // 
        classType: null
      },
      ruleValidate: {
        year: [
          { required: true, message: '', trigger: 'change' }
        ],
        termId: [
          { required: true, message: '', trigger: 'change' }
        ],
        gradeId: [
          { required: true, type: 'number', message: '', trigger: 'change' }
        ],
        subjectId: [
          { required: true, message: '', trigger: 'change' }
        ],
        classLevel: [
          { required: true, message: '', trigger: 'change' }
        ],
        serviceCenterId: [
          { required: true, message: '', trigger: 'change' }
        ],
        classtimeTimeClassify: [
          { required: true, message: '', trigger: 'change' }
        ],
        classtimeTimePeriod: [
          { required: true, message: '', trigger: 'change' }
        ],
        receptionEmpNo: [
          { required: true, message: '', trigger: 'change' }
        ],
        status: [
          { required: true, type: 'number', message: '', trigger: 'change' }
        ],
        classId: [
          { required: true, message: 'ID', trigger: 'change' }
        ],
        failReason: [
          { required: true, message: '', trigger: 'change' },
          { maxlength: 200, message: '200', trigger: 'change' }
        ]
      },
      columns: [
        {
          title: '',
          key: 'name',
          align: 'center'
        },
        {
          title: '',
          key: 'phone',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h(TalDecodeText, {
              props: {
                uid: params.row.uid,
                encodeText: params.row.phone,
                type: 'phone'
              }
            })
          }
        },
        {
          title: '',
          key: 'studentCard',
          align: 'center'
        },
        {
          title: '',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '',
          key: 'termName',
          align: 'center'
        },
        {
          title: '',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '',
          key: 'classLevelName',
          align: 'center'
        },
        {
          title: '',
          key: 'classTypeName',
          align: 'center'
        },
        {
          title: '',
          key: 'serviceCenterName',
          align: 'center'
        }, {
          title: '',
          key: 'classtimeTimeClassify',
          align: 'center'
        }, {
          title: '',
          key: 'classtimeTimePeriod',
          align: 'center'
        }, {
          title: '',
          key: 'intention',
          align: 'center'
        }, {
          title: '',
          key: 'diagnosisScore',
          align: 'center',
          render: (h, params) => {
            const text = params.row.diagnosisScore === 1 ? '' : ''
            return h('span', text)
          }
        }, {
          title: '',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', this.cuanbanStatusMap[params.row.status])
          }
        }
      ],
      tableData: [],
      tableLoading: false,
      studentDemandListSorted: [], // 
      confirmLoading: false, // 
      assessLogs: []// 
    }
  },
  mounted () {
    Object.assign(this.formData, { cityCode: this.parentFormData.cityCode })
    this.initFormOptions()

    // 
    if (this.type === 1) {
      this.groupStudentDemandList()
    }

    if (this.type === 2 || this.type === 3) {
      this.queryDetailById()
    }
  },
  methods: {
    handleStatusChange (val) {
      console.log('val', val)
      this.$nextTick(() => {
        this.$refs.formValidate2.resetFields()
        this.formData.status = val
      })
    },
    // 
    async queryDetailById () {
      try {
        const params = {
          accessId: this.accessId,
          cityCode: this.formData.cityCode
        }
        this.tableLoading = true
        const res = await queryDetailById(params)

        Object.assign(this.formData, res, {
          assembleClassDemandIds: null,
          assessLogs: null
        })

        if (this.type === 3) { // 
          this.formData.status = 1
          this.formData.remark = ''
        }

        this.serviceCenterList = [
          { schoolId: res.serviceCenterId, name: res.serviceCenterName }
        ]

        const studentDemandListSorted = await this.setStudentLevel(res.assembleClassDemandIds || [])
        this.studentDemandListSorted = studentDemandListSorted
        this.assessLogs = Object.freeze(res.assessLogs)
      } catch (error) {
        this.$Message.error({ content: `: ${error.message}`, duration: 5 })
      }

      this.tableLoading = false
    },
    // 
    groupStudentDemandList () {
      // 
      const groupList = new GroupMultiple(
        this.studentDemandList,
        ['year', 'termId', 'gradeId', 'subjectId', 'classLevel', 'serviceCenterId', 'classtimeTimeClassify', 'classtimeTimePeriod']).group().sort((a, b) => b.value.length - a.value.length)
      const keys = groupList[0].value[0]
      // 
      this.studentDemandListSorted = []
      groupList.forEach(item => {
        this.studentDemandListSorted.push(...item.value)
      })

      Object.assign(this.formData, {
        year: keys.year,
        termId: keys.termId,
        termName: keys.termName,
        subjectId: keys.subjectId,
        subjectName: keys.subjectName,
        gradeId: keys.gradeId,
        gradeName: keys.gradeName,
        classLevel: keys.classLevel,
        classLevelName: keys.classLevelName,
        serviceCenterId: keys.serviceCenterId,
        serviceCenterName: keys.serviceCenterName,
        classtimeTimePeriod: keys.classtimeTimePeriod.split(',')[0],
        classtimeTimeClassify: keys.classtimeTimeClassify.split(',')[0],
        remark: '',
        assembleClassDemandIds: this.studentDemandListSorted.map(item => item.id), // ids
        intentionNumber: this.studentDemandListSorted.length, // 
        classType: keys.classType
      })
      this.serviceCenterList = [{
        schoolId: this.formData.serviceCenterId,
        name: this.formData.serviceCenterName
      }]
    },
    // 
    apply () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          this.$refs.formValidate2.validate((_valid) => {
            if (_valid) {
              this.confirmLoading = true
              const params = Object.assign({}, this.formData, {
                receptionEmpName: this.formData.receptionEmpName.split('-')[0]
              })
              console.log(params)
              submit(params).then(() => {
                setTimeout(() => {
                  this.$Message.success({ content: '' })
                  this.$emit('close')
                  this.$emit('success')
                  this.confirmLoading = false
                }, 1000)
              }).catch(error => {
                this.confirmLoading = false
                this.$Message.error({ content: `: ${error.message}`, duration: 5 })
              })
            }
          })
        }
      })
    },
    // 
    approval () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          this.$refs.formValidate2.validate((_valid) => {
            if (_valid) {
              let remark = ''
              if (this.formData.status === 2) {
                remark = this.formData.failReason
              } else {
                remark = this.formData.remark
              }

              this.confirmLoading = true
              const params = {
                accessId: this.formData.id,
                status: this.formData.status,
                remark,
                classId: this.formData.classId,
                cityCode: this.formData.cityCode
              }
              console.log(params)
              operate(params).then(() => {
                setTimeout(() => {
                  this.$Message.success({ content: '' })
                  this.$emit('close')
                  this.$emit('success')
                  this.confirmLoading = false
                }, 1000)
              }).catch(error => {
                this.confirmLoading = false
                this.$Message.error({ content: `: ${error.message}`, duration: 5 })
              })
            }
          })
        }
      })
    },
    confirm () {
      if (this.type === 1) {
        this.apply()
      } else if (this.type === 3) {
        this.approval()
      }
    },
    initFormOptions () {
      this.getBaseDataTermList()
      this.getYears()
      this.getSubjectList()
      this.getGradeList()
      this.getTimeList()
      this.getTimeTypeList()
      this.queryLevelDegreeList()
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .ivu-form-item-label {
  white-space: nowrap;
}

/deep/ .disabled-required .ivu-form-item-label::before {
  display: none;
}
</style>
