<template>
  <Modal
    v-model="schoolState"
    ok-text=""
    cancel-text=""
    title=""
    :mask-closable="false"
    :closable="true"
    @on-visible-change="handleEvents"
    :footer-hide="true"
    width="80%"
  >
    <div class="school-manage">
      <div class="school-manage-left">
          <div class="manege-left-row select-box">
            <Select v-model="schoolCode" :disabled="true" class="select-active" style="width:130px;margin-right: 10px;" placeholder="" @on-change="handleGetDistrict">
              <Option v-for="item in cityList" :value="item.id" :key="item.id">{{item.name}}</Option>
            </Select>
            <Select v-model="districtId" style="width:130px;margin-right: 10px;" placeholder="" @on-change="handleDistrict">
              <Option v-for="item in districtList" :value="item.id" :key="item.id">{{item.name}}</Option>
            </Select>
            <Select v-model="level" style="width:130px" placeholder="">
              <Option v-for="item in levelList" :value="item.id" :key="item.id">{{item.name}}</Option>
            </Select>
            <div class="select-icon">*</div>
          </div>
        <div class="manege-left-row">
          <Input v-model="school" @on-enter="handleSearch()" placeholder="" style="width:200px;margin-right: 20px;" />
          <Button type="primary" @click="handleSearch()"></Button>
        </div>
        <div class="schoolList-box">
          <Table
            ref="selection"
            @on-select="handleSelect"
            @on-select-cancel="handleCancel"
            @on-select-all-cancel="handleCancleAll"
            @on-select-all="handleSelectAll"
            no-data-text="/"
            :columns="columns"
            height="400"
            :data="schoolList"
          >
          </Table>
          <!---->
          <div class="schoolList-total"> <span style="color:#2d8cf0">{{total}}</span> </div>
          <!---->
          <div class="" style="margin-top:20px;text-align: center">
            <Page
              :total="total"
              show-sizer
              :page-size-opts="[10,20,40]"
              @on-page-size-change="handleSizeChange"
              @on-change="handleSearch"
            />
          </div>
        </div>
      </div>
      <div class="school-manage-right">
        <div style="padding: 10px">
          <p style="font-size: 14px;"> <span style="color:#2d8cf0">{{selectList.length}}</span> 
            <Poptip
              confirm
              transfer
              title=""
              style="margin-left: 120px;"
              @on-ok="clearAll"
            >
              <span style="color: deepskyblue;cursor: pointer"></span>
            </Poptip>
          </p>
        </div>
        <div class="mess">
          <div v-for="(item,index) in selectList" :key="index" class="target-list-item">
            <span class="ivu-tag-text">{{item.name}}</span>
            <Icon @click="handleClear(item,index)" type="md-close" style="color: #979797;margin-left: 2px"/>
          </div>
        </div>
      </div>
    </div>
    <div style="text-align: center;">
      <Button style="margin-right:20px" @click="handleManageCancle"></Button>
      <Button @click="handleManageOk" type="primary"></Button>
    </div>
  </Modal>

</template>

<script>
import {
  getCityList,
  getDistrict,
  getGrade,
  getPageList,
  getSchoolBackList,
  bindSchool
} from '@/api/operative'
export default {
  name: 'index',
  data () {
    return {
      cityId: '',
      cityList: [], // 
      districtId: this.propDistrictId, // 
      districtList: [], // 
      level: '', // 
      levelList: [], // 
      school: '', // 
      schoolNum: 0, // 
      selectList: [],
      current: 1, // 
      size: 10,
      total: 0, // 
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '',
          key: 'name',
          align: 'left'
        },
        {
          title: '/',
          key: 'districtName',
          align: 'center'
        },
        {
          title: '',
          key: 'levelName',
          align: 'center'
        },
        {
          title: ` `,
          key: 'provinceName',
          align: 'center'
        }
      ],
      schoolList: [],
      schoolCode: this.propSchoolCode

    }
  },
  props: ['deptId', 'propDistrictId', 'propSchoolCode'],
  methods: {
    handleEvents: function (state) {
      if (state) {
        this.districtId = this.propDistrictId
        this.schoolCode = this.propSchoolCode
        this.handleCityList()// 
        this.handleSchoolBackList()// 
      }
    },
    // 
    handleManageOk: function () {
      if (this.selectList.length === 0) {
        this.$Message.warning('')
        return
      }
      let schoolIds = []
      this.selectList.forEach(item => {
        schoolIds.push(item.id)
      })
      let payloads = {
        deptId: this.deptId,
        schoolIds: schoolIds
      }
      bindSchool(payloads).then(res => {
        if (res.rlt !== true) return
        this.schoolList = []
        this.$emit('handleEimtSchoolList')
        this.$store.commit('service/HANDLE_SCHOOLMANAGE', false)
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
      this.schoolList = []
    },
    // 
    handleManageCancle: function () {
      this.schoolList = []
      this.$store.commit('service/HANDLE_SCHOOLMANAGE', false)
    },
    // 
    handleSchoolBackList () {
      getSchoolBackList({ schoolCode: this.schoolCode, deptId: this.deptId }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.selectList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleCityList () {
      getCityList().then(res => {
        if (res.rlt !== true || !res.data) return
        this.cityId = res.data.find(item => item.id === this.schoolCode).cityId
        this.cityList = res.data
        this.handleGetDistrict(this.cityId, true)
        this.handleGetCityList(this.schoolCode)// 
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleGetDistrict (value, mark) {
      let cityId
      if (mark) {
        cityId = value
      } else {
        let cityItem = this.cityList.find(item => item.id === value)
        cityId = cityItem && cityItem.cityId
        this.cityId = cityId
        this.handleGetCityList(value)// 
      }
      getDistrict({ cityId: cityId }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.districtList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleDistrict: function (value) {
      this.districtId = value
    },
    // 
    handleGetCityList (schoolCode) {
      getGrade({ schoolCode: schoolCode, status: 0 }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.level = res.data[0].id
        this.levelList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleSearch: function (current, size) {
      if (!current) {
        this.current = 1
        this.size = 10
        this.total = 0
      }
      let payloads = {
        schoolCode: this.schoolCode,
        cityId: this.cityId,
        districtId: this.districtId,
        level: this.level,
        likeName: this.school,
        status: 0,
        current: current || this.current,
        size: size || this.size
      }
      getPageList(payloads).then(res => {
        if (res.rlt !== true || !res.data) return
        this.schoolList = res.data && res.data.records
        this.schoolList.forEach(item => {
          item.districtName = item.district.name
          item.levelName = this.levelList.find(each => each.id === item.level).name
        })
        this.total = res.data.total
        // 
        if (this.selectList.length > 0) {
          this.selectList.forEach(item => {
            this.schoolList.forEach(each => {
              // eslint-disable-next-line no-unused-expressions
              each.id === item.id ? each._checked = true : false
            })
          })
        }
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleSelect (selection, row) {
      this.schoolList.find(item => item.id === row.id)._checked = true
      this.selectList.push(row)
    },
    // 
    handleCancel (selection, row) {
      this.schoolList.find(item => item.id === row.id)._checked = false
      this.selectList = this.selectList.filter(item => item.id !== row.id)
    },
    // 
    handleSelectAll: function (selection) {
      this.selectList.forEach(item => {
        selection.forEach(each => {
          if (item.id === each.id) {
            each.hasChoose = true
          }
        })
      })
      this.selectList = this.selectList.concat(selection.filter(item => item.hasChoose !== true))
      this.schoolList.forEach(item => {
        item._checked = true
      })
    },
    // 
    handleCancleAll: function (selection) {
      this.schoolList = this.schoolList.slice()
      this.schoolList.forEach(item => {
        item._checked = false
      })
      this.selectList.forEach(item => {
        this.schoolList.forEach(each => {
          if (item.id === each.id) {
            item.leave = true
          }
        })
      })
      this.selectList = this.selectList.filter(item => !item.leave)
    },
    // 
    handleClear: function (row, index) {
      this.schoolList = this.schoolList.slice()
      let thisItem = this.schoolList.find(item => item.id === row.id)
      if (thisItem) {
        thisItem._checked = false
      }
      this.selectList = this.selectList.filter(item => item.id !== row.id)
    },
    clearAll: function () {
      this.selectList = []
      this.schoolList = this.schoolList.slice()
      this.schoolList.forEach(item => {
        item._checked = false
      })
    },
    handleSizeChange: function (size) {
      this.size = size
      this.handleSearch(this.current, size)
    }
  },
  computed: {
    schoolState: {
      get () {
        return this.$store.state.service.schoolState
      },
      set (val) {
        this.$store.state.service.schoolState = val
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .school-manage{

  }
  /deep/ .school-manage-left{
    width: 65%;
    display: inline-block;
    .manege-left-row{
      margin-bottom:20px;
    }
  }
  .school-manage-right{
    width: calc( 35% - 40px );
    margin-left: 40px;
    float: right;
    display: inline-block;
  }
  .schoolList-box{
    position: relative;
    .schoolList-total{
      position: absolute;;
      right: 10px;
      top:10px;
    }
  }
  .mess {
    padding: 5px 5px 5px 5px;
    height: 400px;
    overflow: auto;
    border: 1px solid gainsboro;
    margin-left: 2px;
    background: #F7F7F7;
  }
  .cess {
    padding: 5px 5px 5px 5px;
    margin-left: 2px
  }
  .target-list-item {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    margin: 4px 8px 4px 0;
    padding: 0 8px;
    border: 1px solid #e8eaec;
    border-radius: 3px;
    background: #ffffff;
    font-size: 12px;
    vertical-align: middle;
    opacity: 1;
    overflow: hidden;
    cursor: pointer;
  }
  .select-box{
    position: relative;
    .select-active{
      .ivu-select-selection{
        padding-left: 10px;
      }
    }
    .select-icon{
      color: red;
