<template>
  <Modal
    v-model="serviceState"
    @on-ok="handleChoose"
    @on-cancel="handleCancle"
    ok-text=""
    cancel-text=""
    title=""
    :mask-closable="false"
    :closable="true"
    @on-visible-change="handleEvents"
    width="700"
    class="server-wrapper"
  >
    <div class="select-box" style="height: 100px">
      <Select v-model="schoolCode" class="select-active" style="width:130px;margin-right: 10px;" placeholder="" @on-change="handleGetDistrict">
        <Option v-for="item in cityList" :value="item.id" :key="item.id">{{item.name}}</Option>
      </Select>
      <Select v-model="districtId" style="width:130px;margin-right: 10px;" placeholder="" @on-change="getServiceCenter">
        <Option v-for="item in districtList" :value="item.id" :key="item.id">{{item.name}}</Option>
      </Select>
      <Select v-model="bindServerId" style="width:130px" placeholder="" @on-change="handleServiceCenter">
        <Option v-for="item in serviceCenterList" :value="item.id" :key="item.id">{{item.name}}</Option>
      </Select>
      <div class="select-icon">*</div>
    </div>
  </Modal>

</template>

<script>
import {
  getCityList,
  getDistrict,
  getServiceCenter,
  bindServiceCenter
} from '@/api/operative'
export default {
  name: 'serverCenter',
  props: ['propSchoolCode', 'propDistrictId', 'propServerId'],
  data () {
    return {
      cityId: '', // id
      serviceAreaName: '', // 
      serviceCenter: '', // id
      serviceCenterName: '', // 
      cityList: [], // 
      districtList: [], // 
      serviceCenterList: [], // 
      // 
      schoolCode: this.propSchoolCode,
      districtId: this.propDistrictId,
      serverId: '',
      bindServerId: this.propServerId,
      canLoad: true
    }
  },
  computed: {
    serviceState: {
      get () {
        return this.$store.state.service.serviceState
      },
      set (val) {
        this.$store.state.service.serviceState = val
      }
    }

  },
  methods: {
    handleEvents: function (state) {
      if (state) {
        this.schoolCode = this.propSchoolCode
        this.districtId = this.propDistrictId
        this.bindServerId = this.propServerId
        this.canLoad = true
        this.getCityList()
      } else {
        this.canLoad = false
      }
    },
    // 
    handleChoose: function () {
      bindServiceCenter({ id: this.bindServerId }).then(res => {
        if (res.rlt !== true) return
        this.$emit('handleBindSchoolCode', this.schoolCode)// 
        this.serviceSite = `${this.serviceAreaName}-${this.serviceCenterName}`
        this.$emit('handleChoose', this.serviceSite)// 
        this.$emit('handleEimtSchoolList', this.bindServerId)// 
        this.$emit('handleDistrict', this.districtId)// 
        this.$emit('handleServer', this.serverId)// 
        this.$store.commit('service/HANDLE_SERVICECENTER', false)// 
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleCancle: function () {
      this.schoolCode = ''
      this.districtId = ''
      this.bindServerId = ''
      this.canLoad = false
    },
    // 
    getCityList () {
      if (!this.canLoad) return
      getCityList().then(res => {
        if (res.rlt !== true || !res.data) return
        this.cityList = res.data
        if (this.schoolCode) {
          this.cityId = res.data.find(item => item.id === this.schoolCode).cityId
        } else {
          this.cityId = res.data[0].cityId
          this.schoolCode = res.data[0].id
        }
        this.handleGetDistrict(this.cityId, true)
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleGetDistrict (value, mark) {
      if (!this.canLoad) return
      let cityId
      if (mark) {
        cityId = value
      } else {
        let cityItem = this.cityList.find(item => item.id === value)
        cityId = cityItem && cityItem.cityId
        this.cityId = cityId
      }
      getDistrict({ cityId: cityId }).then(res => {
        if (res.rlt !== true || !res.data) return
        let data = res.data
        if (data.length > 0) {
          this.districtList = res.data
          let districtItem = res.data.find(item => item.id === this.districtId)
          this.serviceAreaName = districtItem && districtItem.name
          if (!districtItem) {
            this.districtId = res.data[0].id
            this.serviceAreaName = res.data[0].name
          }
          this.getServiceCenter({ schoolCode: this.schoolCode, districtId: this.districtId })
        } else {
          this.serviceAreaName = ''
          this.serviceCenterName = ''
          this.serviceCenterList = []
        }
        this.districtList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    getServiceCenter (payloads) {
      if (!this.canLoad) return
      // 
      let mark = typeof payloads
      let arg
      if (mark === 'object') {
        arg = payloads
      } else {
        let item = this.districtList.find(item => item.id === payloads)
        this.serviceAreaName = item.name
        this.serverId = item.schoolId
        arg = { schoolCode: this.schoolCode, districtId: payloads }
      }
      getServiceCenter({ ...arg, disable: 0 }).then(res => {
        if (res.rlt !== true || !res.data) return
        let data = res.data
        if (data.length > 0) {
          let bindServerItem = res.data.find(item => item.id === this.bindServerId)
          if (!bindServerItem) {
            this.bindServerId = res.data[0].id
            this.serverId = res.data[0].schoolId
            this.serviceCenterName = res.data[0] && res.data[0].name
          } else {
            this.serverId = bindServerItem.schoolId
            this.serviceCenterName = bindServerItem.name
          }
        } else {
          this.serverId = ''
          this.bindServerId = ''
          this.serviceCenterName = ''
        }
        this.serviceCenterList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: ':' + err.message, duration: 5 })
      })
    },
    // 
    handleServiceCenter: function (value) {
      if (!this.canLoad) return
      let serviceItem = this.serviceCenterList.find(item => item.id === value)
      this.serverId = serviceItem && serviceItem.schoolId
      this.bindServerId = value
      this.serviceCenterName = serviceItem && serviceItem.name
    }
  }
}
</script>

<style lang="less" scoped>
  .server-wrapper{

  }
  /deep/ .select-box{
    position: relative;
    .select-active{
      .ivu-select-selection{
        padding-left: 10px;
      }
    }
    .select-icon{
      color: red;
      position: absolute;
      left: 8px;
      top: 8px;
      font-size: 16px;
    }

  }
</style>
