<template>
  <Table
    border
    :loading="loading"
    :columns="detailCols"
    :data="stuList"
  ></Table>
</template>
<script>
import CustomerMixin from '@/components/mixin/customerDetail'
export default {
  name: 'StuDetailExpand',
  mixins: [ CustomerMixin ],
  props: {
    stuList: Array,
    loading: Boolean
  },
  data () {
    return {
      detailCols: [
        {
          title: '',
          type: 'index',
          align: 'center'
        },
        {
          title: '',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return params.row.stuLoginName ? h('a', {
              on: {
                click: () => {
                  const stu = {
                    card: params.row.stuLoginName,
                    cityCode: params.row.areaCode
                  }
                  this.goCustomerDetail(stu)
                }
              }
            }, params.row.name || '--') : h('span', params.row.name || '--')
          }
        },
        {
          title: '',
          key: 'stuLoginName',
          align: 'center'
        },
        {
          title: '',
          key: 'isAssemble',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isAssemble ? '' : '')
          }
        },
        {
          title: '',
          key: 'registStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.registStatusMap[params.row.registStatus] || '--')
          }
        },
        {
          title: '',
          key: 'payStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.payStatus ? '' : '')
          }
        },
        {
          title: '',
          key: 'creator',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.creator === null ? '--' : (params.row.creator || ''))
          }
        },
        {
          title: '',
          key: 'regWay',
          align: 'center',
          render: (h, params) => {
            return h('span', this.regWayMap[params.row.regWay] || '--')
          }
        },
        {
          title: '',
          key: 'regTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.regTime || '--')
          }
        },
        {
          title: '',
          key: 'payTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.payTime || '--')
          }
        },
        {
          title: '',
          key: 'address',
          align: 'center',
          render: (h, params) => {
            return params.row.registStatus ? '' : h('a', {
              on: {
                click: () => {
                  this.$emit('apply', params.row)
                  // this.applyParams.classId = params.row.classId
                  // this.applyParams.studentId = params.row.studentId
                  // this.applyParams.cityCode = params.row.areaCode
                  // this.applyShow = true
                }
              }
            }, '')
          }
        }
      ],
      registStatusMap: {
        0: '',
        1: '',
        2: ''
      },
      regWayMap: {
        0: '',
        1: '',
        2: '',
        3: 'App', // Android
        4: 'App', // IOS
        5: 'CRM',
        6: '',
        7: '',
        8: ''
      }
    }
  }
}
</script>
